import React from "react";
import Text from "../text";
import "./section.scss";

var touchStart, touchEnd;
class section extends React.Component {
	handleWheel(event) {
		event.deltaY >= 0 ? this.props.wheelDown() : this.props.wheelUp();
	}
	handleTouchStart(event) {
		touchStart = event.targetTouches[0].clientX;
	}
	handleTouchMove(event) {
		touchEnd = event.targetTouches[0].clientX;
	}
	handleTouchEnd(event) {
		touchStart - touchEnd < 0
			? this.props.wheelUp()
			: this.props.wheelDown();
	}
	render() {
		return (
			<section
				id={this.props.id}
				ref={this.props.reference}
				onWheel={(event) => {
					this.handleWheel(event);
				}}
				onTouchStart={(event) => {
					this.handleTouchStart(event);
				}}
				onTouchMove={(event) => {
					this.handleTouchMove(event);
				}}
				onTouchEnd={(event) => {
					this.handleTouchEnd(event);
				}}
				className={
					this.props.extraClasses
						? `section ${this.props.extraClasses}`
						: "section"
				}
			>
				{this.props.children}
			</section>
		);
	}
}
section.defaultProps = {
	wheelUp: function () {
		return;
	},
	wheelDown: function () {
		return;
	},
	refProp: null,
	children: <Text>section</Text>,
};
export default section;
